import React, { Component } from 'react'
import {BrowserRouter, Route } from 'react-router-dom'
import ReactDOM from 'react-dom'


//Components 

import Home from './home/home.js'
import Reminders from './reminders/reminders.js'
import Header from './page-componentss/header/header.js'
import Sidebar from './page-componentss/sidebar/sidebar.js'



class App extends Component {
   state = {
         sidebar:'closed'
   }


   sidebarUpdate = (value) => {
      this.setState({
         sidebar:value
      })
   }

   render(){
      return (
         <BrowserRouter>
            <Header sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <Sidebar sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <Route path='/' exact component = {Home} />
            <Route path='/reminders' component = {Reminders} />
            <Route path='/bin' component = '{Bin}' />
            {/* <Route path='/' component = 'Home' /> */}
            {/* <Footer/>            */}
         
         
         
         </BrowserRouter>
      )
   }

}

ReactDOM.render(<App/>,document.getElementById('root'))