import React, { Component } from 'react';
import RenderNotes from '../home/savednotes';

class NewNote extends Component{
    state = {
        userinput:'',
        titleInput:'',
        status:'writing',
        bin:[],
        height:'0px'
     }
     
     animationCard = () => {
            if (this.state.height === '0px'){this.setState({height:'auto'})}
            if (this.state.height === 'auto'){this.setState({height:'0px'})}

     }
     // Title Change Event Handler
     titleInputChange = (event) => {
        this.setState({titleInput:event.target.value})
     }

     // Notes Typed Change handler
     userInputChangeHandler = (event) => {
        this.setState({userinput:event.target.value});
     }
   
     // Save Button onClick Function

     saveNote = () => { if (this.state.userinput !==''){
         var length =
         this.props.JSONnotes.length 
         var i = length + 1
         this.props.JSONnotes.push( 
            { id : i,
             title : this.state.titleInput,
              note: this.state.userinput 
            } 
            ) 
            for (var j = 0;j <= length;j++) {
               this.setState({
                  userinput:'',
                  titleInput:'',
                  status:j }) 
            }
           
             this.clearNote() 
             this.updateNotes() } }
     
    // Clear Button onClick Function call
     clearNote = () => {
        document.getElementById('titleInputBox').value = '';
        document.getElementById('userInputBox').value = '';
     }
     // Update JSON object as String
     updateNotes = () => {
      //   var finalJSON = JSON.stringify(this.props.JSONnotes);
      //  console.log(this.props.JSONnotes);
     }


     // Edit note State Change
     editNote = (id,title,note) => {
      
      document.getElementById('titleInputBox').value = `${title}`
      document.getElementById('userInputBox').value = `${note}`;

      this.setState({
         userinput:`${title}`,
         titleInput:`${note}`
     })
   }
     
     render(){
        return(
            <div className = "parent">
               <div className = "circle">
               <div className = "btn-Small" onClick={this.animationCard}> <i className="fas fa-plus"></i> </div>
               </div>
               
                    <div style = {{height:`${this.state.height}`}}className='card'>
                           
                            <p className="heading-1">Title</p>
                            <textarea type="text" id="titleInputBox" onChange = {this.titleInputChange} className = "titlebox" placeholder = "Give me a Name"></textarea>
            
                            <p className="heading-2">enWrite Here</p>
                             <textarea type="text" id="userInputBox"onChange = {this.userInputChangeHandler} className ="inputBox" placeholder="What's On Your Mind"></textarea>
                         <div>
                            <button className="button" align="center" onClick={this.saveNote}>Save</button>
                             <button className='button' onClick={this.clearNote}>Clear</button>
                         </div>
                    </div>
                    <br/>
                    
                     <RenderNotes 
                     bin = {this.state.bin}
                     editNote = {this.editNote}
                     userinput = {this.state.userinput}
                     JSONnotes={this.props.JSONnotes}
                     {...this.props}/>
                    
            </div>
        )
     }
    
};

export default NewNote;